/* ProductInput.js */
.create {
  padding: 20px 0;
}

.create p~p {
  margin-top: 10px;
}

.create label {
  display: block;
  width: 100%;
  margin-bottom: 5px;
}

.create input {
  display: block;
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
}

.create .submit_btn {
  background: var(--base_color);
  border: none;
  color: #fff;
  cursor: pointer;
}

/* Product.js */
.product_list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 10px;
  padding: 20px 0;
}

.product_list li {
  flex: 0 0 calc(50% - 5px);
}

.product_list img {
  width: 100%;
  transition: 0.3s;
}

.product_list p~p {
  margin-top: 5px;
}

.product_list p:nth-of-type(2) {
  font-weight: bold;
  font-size: 16px;
  color: #e04e4e;
  text-align: right;
}

.product_list .btn {
  padding: 5px;
  background: #e04e4e;
  border: none;
  color: #fff;
  cursor: pointer;
}

/* hover */
.product_list li img:hover {
  filter: brightness(105%)
}