@charset "utf-8";

header {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  background: #fff;
  text-align: center;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}

header h1 img {
  width: 140px;
}

main {
  padding: 15px 10px;
}

footer {
  clear: both;
  background: var(--base_color);
  color: #fff;
  text-align: center;
  line-height: 40px;
}

footer address {
  font-style: normal;
}

.top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  background: #a82a2a;
  border: none;
  color: #fff;
  border-radius: 50%;
  cursor: pointer;
}